// src/plugins/axios.js (ou onde seu plugin estiver)
import axios from 'axios'
import router from '@/router'

// Cria uma inst√¢ncia do axios
const api = axios.create({
  // AQUI EST√Å A CORRE√á√ÉO MAIS IMPORTANTE:
  // Garantimos que a URL base seja sempre a do nosso backend.
  baseURL: 'http://localhost:3000/api', 
  timeout: 10000,
});

// Interceptor para ADICIONAR o token em cada requisi√ß√£o
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('authToken');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Interceptor para TRATAR erros de resposta
api.interceptors.response.use(
  (response) => {
    // Se a requisi√ß√£o for bem-sucedida, retorna os dados diretamente
    return response.data;
  },
  (error) => {
    if (error.response) {
      const status = error.response.status;
      if (status === 401) { // N√£o autorizado (token inv√°lido/expirado)
        localStorage.removeItem('authToken');
        localStorage.removeItem('user');
        router.push('/login');
        alert('Sua sess√£o expirou. Por favor, fa√ßa login novamente.');
      }
    }
    return Promise.reject(error);
  }
);

// Plugin Vue para instalar a inst√¢ncia 'api' como this.$axios
const axiosPlugin = {
  install: (app) => {
    app.config.globalProperties.$axios = api;
    app.provide('axios', api);
  }
}

export default axiosPlugin;